import login from '../../components/login'
import {
	api
} from '../../const'
import {
	barId
} from '../../const'
import {
	checkT
} from '../../utils/net'


export default {
	components: {
		login
	},

	watch: {
		content(next) {}
	},
	data() {
		return {
			type: {
				pickerData: ['故事贴', '一日一记'],
				index: 1
			},
			anon: {
				pickerData: ['非匿名', '匿名'],
				index: 0
			},
			title: '',
			content: '',
			loginVisible: false,
			tab: 'markdown', // or preview
			t: '',
			imgList: []
		}
	},
	methods: {
		async uploadImg() {
			var that = this;
			uni.chooseImage({
				count: 1, //最多可以选择的图片总数
				sizeType: ['compressed'], // 只能是原图
				sourceType: ['album'], // 只能是相册
				success: function(res) {
					// 返回选定照片的本地文件路径列表，tempFilePath可以作为img标签的src属性显示图片
					var tempFilePaths = res.tempFilePaths;
					uni.showToast({
						title: '正在上传...',
						icon: 'loading',
						mask: true,
						duration: 20000
					})


					uni.uploadFile({
						url: api,
						filePath: tempFilePaths[0],
						name: "file",
						header: {
							"Content-Type": "multipart/form-data"
						},
						formData: {
							"act": "img.upload",
							"type": 2
						},
						dataType: "json",
						success: function(res) {
							var data = JSON.parse(res.data);
							var file = new Object();
							file.url = data.url;
							file.imgId = data.imgId;
							that.imgList.push(file);
							uni.hideToast();
						}
					});

				}
			})
		},
		delImg(index) {
			this.imgList.splice(index, 1);
		},
		bindPickerChange(e) {
			this.type.index = e.mp.detail.value
		},
		bindPickerChange1(e) {
			this.anon.index = e.mp.detail.value
		},
		changeTab(e) {
			this.tab = e.target.dataset.tab
		},
		closeModalEvent() {
			console.log("fdafdas")
			this.loginVisible = false
			this.t = uni.getStorageSync("t")
			console.log(this.t);
		},
		async handle() {
			if (this.title.length == 0) {
				uni.showToast({
					title: '标题不能为空',
					icon: 'none',
					duration: 2000
				})
				return;
			}

			if (this.content.length == 0) {
				uni.showToast({
					title: '内容不能为空',
					icon: 'none',
					duration: 2000
				})
				return
			}

			var imgIdList = [];
			for (var i = 0; i < this.imgList.length; i++) {
				imgIdList.push(this.imgList[i].imgId)
			}
			var that = this;
			if (this.type.index == -1) {
				uni.showToast({
					title: '请选择类型',
					icon: 'none',
					duration: 2000
				})
			}
			if (this.anon.index == -1) {
				uni.showToast({
					title: '请选择是否匿名',
					icon: 'none',
					duration: 2000
				})
			}
			this.$http({
				act: 'post.add',
				t: this.t,
				barId: `${barId}`,
				title: this.title,
				content: this.content,
				imgList: JSON.stringify(imgIdList),
				type: this.type.index,
				anonymous: this.anon.index
			}, function(res) {
				if (!response.data.code) {
					uni.showToast({
						title: '发帖成功',
						icon: 'none',
						duration: 2000
					})
					that.title = ''
					that.content = ''
					uni.switchTab({
						url: `../index/main`
					})
				} else if (response.data.code == 6) {
					uni.showToast({
						title: '您发的标题或内容违规，请修改后重发',
						icon: 'none',
						duration: 2000
					})
				}
			})
		}
	},
	onShow() {
		var that = this;
		this.t = uni.getStorageSync("t")
		console.log("publish +" + this.t)
		checkT(this.t,
			function() {
				that.loginVisible = true
			},
			function() {});
	}
}
